.video-wrap {
  position: relative;
  height: 450px;
  overflow-y: hidden;
  video {
    position: relative;
    top: -150px;
    left: 0;
    right: 0;
    width: 100vw;
    z-index: -2; }
  .video-overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.6);
    z-index: -1; }
  .header-title {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    color: #fff;
    z-index: 0;
    align-items: center;
    justify-content: center;
    h1 {
      font-size: 8rem; }
    h3 {
      font-size: 2rem;
      letter-spacing: 3rem; } } }

